.main__single-p {
  .brief {
    padding-top: 0;
  }
}

.single-p {
  display: flex;

  @include breakpoint(max-width 768px) {
    flex-wrap: wrap;
  }
}

.single-p__layout {
  width: 57%;
  margin-right: 45px;

  @include breakpoint(max-width 1200px) {
    width: 50%;
  }

  @include breakpoint(max-width 992px) {
    width: 100%;
    margin-right: 0;
  }

  .single-p__fancybox {
    display: block;
    width: 100%;

    img {
      width: 100%;
    }
  }
}

.single-p__desc {
  width: 43%;
  height: max-content;
  padding-top: 20px;

  @include breakpoint(max-width 1200px) {
    width: 50%;
  }

  @include breakpoint(max-width 992px) {
    width: 100%;
    margin-top: 25px;
    margin-bottom: 50px;
  }

  .single-p__title {
    position: relative;
    top: -10px;
    margin: 0;
    font-family: $GilroySemibold;
    font-size: 30px;
    font-weight: normal;
    color: #282828;
    line-height: 1.2;

    @include breakpoint(max-width 992px) {
      text-align: center;
    }
  }

  & > p {
    margin: 0;
    margin-top: 10px;
    font-size: 15px;
    color: #828181;
    line-height: 1.4;

    &:last-of-type {
      padding-bottom: 30px;
      border-bottom: 1px solid #e8e8e8;

      @include breakpoint(max-width 992px) {
        padding-bottom: 20px;
      }
    }
  }

  .order__ask {
    @include breakpoint(max-width 768px) {
      margin-right: 20px;
    }

    @include breakpoint(max-width 450px) {
      width: 100%;
      margin-right: 0;
      text-align: center;
    }
  }

  .order__ask-title {
    margin: 0;
    font-family: $GilroySemibold;
    font-size: 22px;
    font-weight: normal;
    color: #282828;

    @include breakpoint(max-width 450px) {
      width: 100%;
    }
  }

  .order__ask-text {
    margin: 0;
    margin-top: 5px;
    font-size: 18px;
    color: #282828;
  }

  .order__btn {
    display: block;
    width: 160px;
    padding: 10px 0;
    font-family: $GilroySemibold;
    font-size: 17px;
    color: white;
    text-align: center;
    letter-spacing: 0.87px;
    border-radius: 21px;
    background-color: #ff4834;
    transition: transform 0.2s;

    &:hover, &:active, &:focus {
      color: white;
      transform: scale(0.9);
      transition: transform 0.2s;
    }

    @include breakpoint(max-width 992px) {
      margin-top: 10px;
    }

    @include breakpoint(max-width 768px) {
      margin-top: 0;
    }

    @include breakpoint(max-width 450px) {
      margin: 0 auto;
      margin-top: 10px;
    }
  }

  .single-p__stock {
    padding-top: 30px;
    padding-bottom: 30px;

    @include breakpoint(max-width 992px) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  .single-p__describe {
    padding-bottom: 30px;

    @include breakpoint(max-width 992px) {
      padding-bottom: 20px;
    }
  }

  .stock__title {
    display: flex;
    align-items: center;
    margin: 0;
    font-size: 22px;

    @include breakpoint(max-width 450px) {
      justify-content: center;
    }

    img {
      margin-right: 10px;
    }
  }

  .stock__desc {
    margin: 0;
    margin-top: 20px;
    font-size: 18px;

    @include breakpoint(max-width 992px) {
      margin-top: 10px;
    }

    @include breakpoint(max-width 450px) {
      text-align: center;
    }
  }

  .stock__red {
    font-family: $GilroyBold;
    font-weight: normal;
    color: #ff4834;
  }
}

.single-p__stock,
.single-p__soc,
.single__order {
  border-bottom: 1px solid #e8e8e8;
}

.single-p__desc p, .single-p__desc li, .single-p__desc span, .single-p__desc strong {
  font-size: 15px;
  color: #828181;
}